import { clsx } from 'clsx';
import { Slot } from '@radix-ui/react-slot';
import { ReactNode } from "react";

export interface ButtonProps {
  children: ReactNode;
  asChild?: boolean;
}

export function Button({ children, asChild }: ButtonProps) {
  const Comp = asChild ? Slot : 'button';

  return(
    <Comp className={clsx(
      'py-4 px-3 bg-green-600 rounded outline-none font-semibold text-green-50 text-sm w-full transition-colors hover:ring-2 focus:ring-2 ring-green-50',
      )}
    >
      {children}
    </Comp>
  );
}
